.container{
    width: 100vw;
    height: fit-content;
}

.header{
    width: 100%;
    height: 200px;
    font-size: 120px;
    text-transform: uppercase;
    text-align: center;
    margin-top: 100px;
}

.inputDiv{
    width: 100vw;
    background-color: bisque;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.inputDiv input{
    width: 30vw;
    padding: 5px;
    margin: 20px;
    border: none;
    border-radius: 10px;
}

button{
    width: 130px;
    padding: 6px;
    border-radius: 10px;
    margin-bottom: 20px;
    background-color: bisque;
    cursor: pointer;
    transition: all 0.3s;
}
button:hover{
    background-color: rgb(255, 206, 147);
}


.list-heading{
    padding-left: 30px;
    padding-top: 20px;
}

.listDataDiv{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;
}

.listData{
    background-color: bisque;
    width: 100vw;
    height: max-content;
    margin-bottom: 30px;
    text-align: center;
    padding: 10px;
}

.buttonDiv{
    width: 100vw;
    display: flex;
    justify-content: center;
}

@media screen and (max-width:600px) {
    .inputDiv input{
        width: 80vw;
    }
    .header{
        font-size: 60px;
        height: 100px;
    }
}